ales.")])])])])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Formas de generarlo"}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('p',{staticClass:"mb-4"},[_vm._v("Existen dos formas de generar un "),_c('i',[_vm._v("Cross docking")]),_vm._v(":")]),_c('ol',{staticClass:"lista-ol--cuadro"},[_c('li',[_c('div',{staticClass:"lista-ol--cuadro__vineta"},[_c('span',[_vm._v("1")])]),_vm._v("Cuando un proveedor envía al CEDI (centro de distribución) un pedido consolidado que va a ser subdividido para reenviar a varios destinatarios.")]),_c('li',[_c('div',{staticClass:"lista-ol--cuadro__vineta"},[_c('span',[_vm._v("2")])]),_vm._v("Cuando al proveedor se le solicita un pedido indicando como subdividirlo, este llega al CEDI, se le adecua un espacio en el muelle y se genera un despacho para el destinatario.")])])])])])]),_c('separador'),_vm._m(12),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("De acuerdo con la Organización Internacional de Normalización - ISO, (2020) La calidad total es uno de los objetivos más ansiados por la logística, aunque también uno de los más esquivos. Los costes y la presión de la competencia hacen muy difícil encontrar la serenidad y el clima idóneo necesarios para el desarrollo de un programa de calidad adecuado, mientras que los problemas operativos se multiplican día a día, precisando sin pausa, más recursos para resolverlos y dando pie a un círculo vicioso que pone en dificultades a no pocas empresas. En consecuencia, implementar un sistema de calidad, en especial en el área logística, supone un importante esfuerzo por conocer perfectamente todos y cada uno de los procesos que se realizan, hasta el punto de poderlos describir con propiedad y exactitud; si alguno no es fácil de definir, casi siempre significa que en él influyen todo tipo de casuísticas, incluyendo el azar, con las previsibles consecuencias de no calidad.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-ri